import React from "react";
import { graphql, navigate } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import {Helmet} from "react-helmet";

import Layout from "../layouts";

export default function detail(props) {
  const item = props.data.contentfulCar;
  const image = getImage(item.image);

  return (
    <Layout>
      <Helmet>
        <title>{item.name}</title>
        <meta name="description" content={item.description.description} />
      </Helmet>
      <div className="carDetail">
        <div className="container">
          <div className="row">
            <div className="col-12 col-md-5">
              <GatsbyImage image={image} alt={item.name} />
            </div>
            <div className="col-12 col-md-7">
              <h2>{item.name}</h2>
              <ul className="car-details">
                <li><span>Price</span>: {item.price}</li>
                <li><span>Description</span>: {item.description.description}</li>
                <li><span>Brand</span>: {item.brand}</li>
                <li><span>Seats</span>: {item.seats}</li>
                <li><span>Year</span>: {item.year}</li>
                <li><span>Fuel</span>: {item.fuel}</li>
                <li><span>Transmission</span>: {item.transmission}</li>
              </ul>
              <button onClick={() => navigate('/contact-us')} className="btn btn-rental" >Rent this car</button>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export const carDetailQuery = graphql`
  query carDetailQuery($id: String!) {
    contentfulCar(id: { eq: $id }) {
      id
      brand
      description {
        description
      }
      fuel
      name
      offer
      price
      seats
      slug
      transmission
      year
      image {
        gatsbyImageData
      }
    }
  }
`;
